.comment--form{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 500px;
    h3{
        letter-spacing: 1.25px;
        text-transform: uppercase;
        font-weight: 400;
        font-size: 1rem;
        margin-bottom: 50px;
    }
    form{
        width: 65%;
    }
    textarea{
        display: block;
        margin-bottom: 10px;
        outline: none;
        border: none;
        border-bottom: 2px solid deepskyblue;
        background: lighten(skyblue, 25%);
        border-radius: 5px;
        height: 215px;
        width: 100%;
        padding: 15px;
        font-size: .9rem;
        letter-spacing: .75px;
    }
    .comment--item--button{
        background: transparent;
        border: 2px solid #b3ecff;
        border-radius: 5px;
        padding: 15px 25px;
        letter-spacing: 1.1px;
        text-transform: uppercase;
        font-weight: 400;
        outline: none;
        margin: 10px 0;
        transition: .35s all ease-in-out;
        &:hover{
            cursor: pointer;
            border: 2px solid deepskyblue;
        }
    }
}

 // Media Qs
 @media only screen and (max-width: 800px){
    .comment--form form {
        width: 90%;
    }
 }
 @media only screen and (max-width: 800px){
    .comment--item {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        &:first-of-type{
                padding-top: 2rem!important;
            }
    }
    .comment--item--userTitle button{
        margin-bottom: 20px;
    }
 }
